@font-face {
  font-family: "Marvin-Round";
  src: url("../fonts/marvin-round.otf") format("opentype");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow-x: hidden; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

* {
  outline: 1px solid yellow; }

body {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  min-width: 320px;
  height: 100%;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  padding-right: 10px;
  overflow-x: hidden;
  font-family: "Marvin Round", "Arial", sans-serif;
  color: #54183c;
  text-transform: uppercase;
  background-image: url("../img/background.jpg");
  backround-repeat: no-repeat;
  background-size: 100%; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.disable-scroll {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden; }

